.contactInfoWrapper {
  display: flex;
  align-items: center;
  padding: 10px 20px;
}

.avatarWrapper {
  position: relative;
  width: 50px;
  height: 50px;
  margin-right: 20px;
}

.avatar {
  border-radius: 50%;
  width: 50px;
}

.contactName {
  line-height: 1.3;
}

.statusIndicatorOnline,
.statusIndicatorOffline {
  position: absolute;
  right: 2px;
  bottom: 2px;
  width: 12px;
  height: 12px;

  border-radius: 50%;
}

.statusIndicatorOnline {
  background-color: var(--online-indicator-color);
  border: 2px solid var(--online-indicator-color);
}

.statusIndicatorOffline {
  background-color: transparent;
  background-color: var(--bg-color);
  border: 2px solid var(--offline-indicator-color);
}
